import { DefaultLayout } from "../../layouts";
import {
  ContactOrganism,
  FooterOrganism,
  HeaderOrganism
} from "../../organisms";

export default function ContactScreen() {
  return (
    <DefaultLayout
      header={<HeaderOrganism />}
      body={
        <>
          <ContactOrganism />
        </>
      }
      footer={<FooterOrganism />}
    />
  );
}